import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-ars-alert-modal',
  templateUrl: './ars-alert-modal.component.html',
  styleUrls: ['./ars-alert-modal.component.scss']
})
export class ArsAlertModalComponent implements OnInit {

  @Input() text: string;
  @Output() close = new EventEmitter();

  constructor() { }

  ngOnInit() {
  }

  closeAlertModal() {
    this.close.emit();
  }

}



