import { Component, OnInit, Output, Input, EventEmitter  } from '@angular/core';

@Component({
  selector: 'app-modal-announcement',
  templateUrl: 'modal-announcement.component.html',
  styleUrls: ['modal-announcement.component.scss']
})
export class ModalAnnouncementComponent implements OnInit {
  @Input() show : boolean = false;
  @Input() cancelBtnShow : boolean  = false;
  @Input() alertTxt : string  = '';
  @Input() contentTxtList: any  = [];
  @Input() headerText: string = '';
  @Input() reDirect : any = '';
  @Output() onClickCancel = new EventEmitter<boolean>();
  @Output() onClickSubmit = new EventEmitter<boolean>();

  constructor(

  ) { }

  ngOnInit() {
  }

  dismiss() : void {
    this.onClickCancel.emit(this.show)
  }

  submit(): void {
    this.onClickSubmit.emit(this.show)
  }

}



