import { Component, OnInit, EventEmitter, Output, OnDestroy } from '@angular/core';
import { Location } from '@angular/common';
import { ListBlockOrder, ListAutoUnblock, QueryAccBlock, ReleaseOrder } from 'app/model/release-order.model';
import { ReleaseOrderService } from '../release-order.service';
import { Router } from '@angular/router';
import { modalService } from '../../services/modalService';
import { AlertService } from 'app/shared/alert/alert.service';
import { PageLoadingService } from 'client-mypartner-lib/src/components/page-loading/page-loading.service';

@Component({
  selector: 'app-order-detail',
  templateUrl: './order-detail.component.html',
  styleUrls: ['./order-detail.component.scss']
})
export class OrderDetailComponent implements OnInit, OnDestroy {
  queryAccBlock: QueryAccBlock;
  listBlockOrderCredit: ListBlockOrder[];
  collSwitch: boolean;
  collapseNum: number;
  collapseCheck: boolean;
  selectRelease: ListBlockOrder[] = [];
  billLength = 0;
  selectAll: boolean;

  heightContent = '';
  myStyles: any;


  constructor(private _location: Location, private pageLoadingService: PageLoadingService, private modalService: modalService, private router: Router, private releaseOrderService: ReleaseOrderService) { }

  ngOnInit() {
    this.myStyles = { 'height': (window.screen.height / 2).toString() + 'px;' }
    this.setHeightContent();
    this.getQueryAccBlock();
    this.onChangeCompany();
    this.releaseOrderService.page.next('detail');
  }

  onChangeCompany() {
    this.releaseOrderService.company.subscribe(value => {
      this.pageLoadingService.openLoading();
      console.log(value)
      if (value === 'AWN') {
        this.queryAccBlock = this.releaseOrderService.getAWNdata();
        this.listBlockOrderCredit = this.releaseOrderService.getAWNdata().listBlockOrderCredit;
        console.log(this.queryAccBlock);
        console.log(this.listBlockOrderCredit);
        console.log(this.releaseOrderService.releaseSelected);
        this.autoRelease(this.queryAccBlock.listBlockOrderCredit);
      } else {
        this.queryAccBlock = this.releaseOrderService.getWDSdata();
        this.listBlockOrderCredit = this.releaseOrderService.getWDSdata().listBlockOrderCredit;
        this.autoRelease(this.queryAccBlock.listBlockOrderCredit);
      }
    });
  }

  setHeightContent() {
    if (window.screen.height >= 1366) {
      this.heightContent = '850px;'
    } else if (window.screen.height >= 1024) {
      this.heightContent = '570px;'
    } else {
      this.heightContent = (window.screen.height / 2).toString() + 'px;'
    }
  }

  getQueryAccBlock() {
    this.releaseOrderService.getQueryAccBlock();
  }

  autoRelease(data) {
    console.log('join autoRelease');
    console.log(this.listBlockOrderCredit);
    console.log(data);
    if (data.length === 1) {
      const result = {
        overDue: this.queryAccBlock.overDue,
        listBlockOrderCredit: this.queryAccBlock.listBlockOrderCredit
      }
      this.releaseOrderService.postReleaseOrder(result).then(async res => {
        if (res.resultCode === "S") {
          console.log(await 'ready to map');
          this.releaseOrderService.releaseSelected.listBlockOrderCredit = await res.listBlockedCreditOrder;
          console.log(await 'ready to router');
          await this.router.navigate(['/release-order/detail']);
          this.pageLoadingService.closeLoading();
        } else {
          this.modalService.alert([true, 'Release Order ไม่สำเร็จ']);
          this.pageLoadingService.closeLoading();
        }
      }).catch(err => {
        this.modalService.alert([true, 'ไม่สามารถ Release order ได้']);
        this.pageLoadingService.closeLoading();
      })
    } else {
      // setTimeout(() => {
      //   this.autoRelease(data);
      // }, 1000)
      this.pageLoadingService.closeLoading();
    }
  }

  checkimage(check: boolean) {
    if (check) {
      return './assets/images/icon/icon-check.png';
    } else {
      return './assets/images/icon/icon-uncheck.png';
    }
  }

  collapseSwitch(check: boolean) {
    if (check) {
      return './assets/images/icon/icon-arrow-bottom-green.png';
    } else {
      return './assets/images/icon/icon-arrow-top-green.png';
    }
  }

  onCollapseSwitch(creditIdx: number, listIdx: number) {
    this.queryAccBlock.listBlockOrderCredit[creditIdx].listAutoUnblockOrderDetailBean[listIdx].collapse = !this.queryAccBlock.listBlockOrderCredit[creditIdx].listAutoUnblockOrderDetailBean[listIdx].collapse;
  }

  selectedCredit(credit: ListBlockOrder, list: ListAutoUnblock, idx: number) {
    console.log(credit);
    console.log(list);
    console.log(idx)

    const result = this.selectRelease.every(val => val.key !== credit.key);
    console.log(result)
    if (result) {
      this.selectRelease.push(credit);
      this.queryAccBlock.listBlockOrderCredit.map(val => {
        if (val.key === credit.key) {
          val.selected = true;
        }
      });
    } else {
      this.selectRelease = this.selectRelease.filter(val => val.key !== credit.key);
      this.queryAccBlock.listBlockOrderCredit.map(val => {
        if (val.key === credit.key) {
          val.selected = false;
        }
      });
    }

    console.log(this.selectRelease);
    this.addReleaseSelect();
  }

  onSelectAll() {
    this.selectAll = true;
    this.queryAccBlock.listBlockOrderCredit.map(val => val.selected = true);
    this.selectRelease = this.queryAccBlock.listBlockOrderCredit;
    this.addReleaseSelect();
  }

  onCancelAll() {
    this.selectAll = false;
    this.queryAccBlock.listBlockOrderCredit.map(val => val.selected = false);
    this.selectRelease = [];
    this.addReleaseSelect();
  }

  addReleaseSelect() {
    this.billLength = this.selectRelease.length;
    this.releaseOrderService.onReleaseSelected({ listBlockOrderCredit: this.selectRelease, overDue: this.queryAccBlock.overDue });
  }

  onCancel() {
    // this.router.navigate(['/index']);
    this._location.back();
  }

  onNext() {
    console.log(this.releaseOrderService.releaseSelected);
    this.releaseOrderService.releaseSelected.listBlockOrderCredit && this.releaseOrderService.releaseSelected.listBlockOrderCredit.length > 0
      ? this.router.navigate(['/release-order/summary'])
      : this.modalService.alert([true, 'Please select release order.']);
  }

  clear() {
    this.queryAccBlock = {};
    this.listBlockOrderCredit = [];
    this.collSwitch = false
    this.collapseNum = 0;
    this.collapseCheck = false
    this.selectRelease = [];
    this.billLength = 0;
    this.selectAll = false
  }

  ngOnDestroy() {
    // this.releaseOrderService.company.unsubscribe();
    this.clear();
    // this.releaseOrderService.awnData.listBlockOrderCredit = [];
    // this.releaseOrderService.awnData.overDue = null;
  }
}



