import { Component, OnInit, Output, EventEmitter, Input } from '@angular/core';
import { IMyDpOptions } from "mydatepicker";

@Component({
  selector: 'app-ars-search',
  templateUrl: './ars-search.component.html',
  styleUrls: ['./ars-search.component.scss']
})
export class ArsSearchComponent implements OnInit {

  @Output() cancel = new EventEmitter();
  @Output() search = new EventEmitter();
  @Input() locationCode: string;

  myDatePickerOptions: IMyDpOptions = {
    // other options...
    dateFormat: "yyyy-mm-dd"
  };
  selDate: any = { year: 0, month: 0, day: 0 };
  public startDate: string = "";
  public endDate: string = "";

  searchDataForm = {
    orderNo: '',
    distributorLocationCode: this.locationCode,
    retailLocationCode: '',
    startDate: '', //202012312050 yyyyMMddHHmm 
    endDate: '', //202012312050 yyyyMMddHHmm 
    channel: 'myPartner'
  }

  showAlert = false;
  alertText: string;

  constructor() { }

  ngOnInit() {
    console.log('this.locationCode = ', this.locationCode);
    //* SET datepicker width
    this.myDatePickerOptions['selectorWidth'] = ((window.innerWidth * 40) / 100).toString() + 'px';
  }

  onStartDateChanged($event) {
    console.log($event);
    this.startDate = $event.formatted;
    this.searchDataForm.startDate = this.convertDate(this.startDate, 'start');
  }

  onEndDateChanged($event) {
    console.log($event);
    this.endDate = $event.formatted;
    this.searchDataForm.endDate = this.convertDate(this.endDate, 'end');
  }

  convertDate(date, mode) {
    const split = date.split('-');
    const hhmm = mode === 'start' ? '0000' : '2359'
    return split[0] + split[1] + split[2] + hhmm;
  }

  onClickCancel() {
    this.cancel.emit(false);
  }

  onClickSearch() {
    this.validate()
      ? this.search.emit(this.searchDataForm)
      : this.showAlert = true;
    console.log(this.searchDataForm);
  }

  validate() {
    let validateCase = true;

    if (this.startDate !== '' || this.endDate !== '') {
      if (this.startDate && this.startDate !== '' && this.endDate && this.endDate !== '') {
        validateCase = true;
      } else {
        validateCase = false;
        this.alertText = 'กรุณาระบุวันที่ From - To ให้ครบถ้วน';
      }
    }
    this.deleteSomeField();
    return validateCase;
  }

  deleteSomeField() {
    this.searchDataForm.distributorLocationCode = this.locationCode;

    if (!this.searchDataForm.orderNo || this.searchDataForm.orderNo === '') {
      delete this.searchDataForm.orderNo;
    }

    if (!this.searchDataForm.retailLocationCode || this.searchDataForm.retailLocationCode === '') {
      delete this.searchDataForm.retailLocationCode;
    }

    if (!this.searchDataForm.startDate || this.searchDataForm.startDate === '') {
      delete this.searchDataForm.startDate;
    }

    if (!this.searchDataForm.endDate || this.searchDataForm.endDate === '') {
      delete this.searchDataForm.endDate;
    }
  }

}



