import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router, NavigationEnd } from '@angular/router';
import { modalService } from '../services/modalService'
declare let $: any;
@Component({
  selector: 'app-release-order',
  templateUrl: './release-order.component.html',
  styleUrls: ['./release-order.component.scss']
})
export class ReleaseOrderComponent implements OnInit {


  public backLink: boolean = false;
  public backLinkStatus: string;
  public active: string;
  public tabStatus: boolean = true;
  public modalShow: boolean = false;
  public alertTxt: string = '';
  public cancelBtn: boolean = false;
  public reDirect: string = '';
  public image: string = '';
  public searchTool: boolean = false

  constructor(private route: ActivatedRoute,
    private router: Router,
    private modalService: modalService) {
    let that = this;

    router.events
      .filter((event) => event instanceof NavigationEnd)
      .map(() => route)
      .subscribe((event) => {
        event.firstChild.data.subscribe(data => {
          console.log('data:', data);
          that.backLink = data.backLink
          that.backLinkStatus = data.backLinkStatus
          that.active = data.active
          that.tabStatus = data.tabStatus
          that.image = data.image
          that.searchTool = data.searchTool
        });
      });

    modalService.modalHandle().subscribe(agv => {
      console.log('agv: ', agv)
      this.modalShow = agv.show
      this.alertTxt = agv.alertTxt
      this.cancelBtn = agv.cancelBtn
      this.reDirect = agv.reDirect
    })

    // this.modalService.alert([true, 'TEST MESSAGE' , false, '/index/main']);
  }

  ngOnInit() {
    $('body').css('background', '#e8e5e5');
  }

}



