import { Injectable } from '@angular/core';
import { Subject } from 'rxjs/Subject';
import { Observable } from 'rxjs/Observable';

@Injectable()
export class DistChannel {
    private distChannelList: any = [
        {
            channel: "11",
            description: "AISCHANNEL"
        },
        {
            channel: "12",
            description: "ONLINE"
        },
        {
            channel: "13",
            description: "DIRECTSALES"
        },
        {
            channel: "14",
            description: "CORPORATE_SALES"
        },
        {
            channel: "15",
            description: "TELECOM"
        },
        {
            channel: "16",
            description: "MODERNTRADE"
        },
        {
            channel: "17",
            description: "RETAILCHAIN"
        },
        {
            channel: "99",
            description: "OTHERS"
        }
    ];

    public getChannelByDescription(description) {
        let result = ""
        this.distChannelList.map(function(obj, i){
            if(obj.description == description){
                result = obj.channel
            }
        });
        return result
    }

}


