import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { TopbarComponent } from "./topbar/topbar.component";
import { ModalAlertComponent } from './modal-alert/modal-alert.component'
import { ModalTopbarComponent } from './modal-topbar/modal-topbar.component'
import { ModalAnnouncementComponent } from './modal-announcement/modal-announcement.component'
import { RouterModule } from '@angular/router';

@NgModule({
  imports: [
    CommonModule,
    RouterModule
  ],
  declarations: [
    ModalAlertComponent,
    TopbarComponent,
    ModalTopbarComponent,
    ModalAnnouncementComponent
  ],
  providers: [
  ],
  exports: [ ModalAlertComponent, TopbarComponent, ModalTopbarComponent, ModalAnnouncementComponent ]
})
export class TopbarSharedModule { }



