import { BrowserModule } from '@angular/platform-browser';
import { NgModule, ModuleWithProviders } from '@angular/core';
import { ServiceShared } from './shared/service.module'
import { Routes, RouterModule } from '@angular/router';
import { CookieService } from "angular2-cookie/core";
import { FormsModule,ReactiveFormsModule } from '@angular/forms';

import { AppComponent } from './app.component';
import { AppRoutingModule } from './app-routing.module';
import { CircleIconModule } from "client-mypartner-lib/src/components/circle-icon/circle-icon.module";
import { AlertService } from "client-mypartner-lib/src/components/alert/alert.service";
import { CardModule } from "client-mypartner-lib/src/components/card/card.module";
import { PipeModule } from "client-mypartner-lib/src/components/pipes/pipe.module";
import { PageLoadingModule } from "client-mypartner-lib/src/components/page-loading/page-loading.module";
import { PageLoadingService } from "client-mypartner-lib/src/components/page-loading/page-loading.service";
import { HttpModule, JsonpModule } from '@angular/http';
import { AuthModule } from 'client-mypartner-lib/src/services/auth/auth.module';
import { NgxCarouselModule } from 'ngx-carousel';
import { AlertModule } from './shared/alert/alert.module';
import { ReleaseOrderModule } from './release-order/release-order.module';
import { ArsOrderModule } from './ars-order/ars-order.module';
import { AuthGuard } from './guard/auth.guard';
import { AuthGuardService } from './guard/auth-guard.service';
import { LandingLoginComponent } from './container/landing-login/landing-login.component';
import { UserInfo } from './services/userInfo';
import { DataForm } from './services/form';
import { Term } from './constant/Term';

export function cookieServiceFactory(): CookieService {
  return new CookieService();
}

@NgModule({
  declarations: [
    AppComponent,
    LandingLoginComponent
  ],
  imports: [
    AuthModule,
    ServiceShared.forRoot(),
    BrowserModule,
    AppRoutingModule,
    CircleIconModule,
    CardModule,
    PipeModule,
    AlertModule,
    PageLoadingModule,
    FormsModule,
    NgxCarouselModule,
    HttpModule,
    ReleaseOrderModule,
    ArsOrderModule,
    ReactiveFormsModule
  ],
  exports: [
    RouterModule
  ],
  providers: [
    AlertService,
    {
      provide: CookieService,
      useFactory: cookieServiceFactory
    },
    PageLoadingService,
    AuthGuard,
    AuthGuardService,
    UserInfo,
    DataForm,
    Term
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



