import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { ServiceShared } from './shared/service.module'
import { AuthGuard } from './guard/auth.guard';
import { LandingLoginComponent } from './container/landing-login/landing-login.component';
// import { ReturnDevicesComponent } from './mychannel-fbb/return-devices/return-devices.component'
//@todo: implement preload strategy

const routes: Routes = [
  {
    path: 'login',
    component: LandingLoginComponent,
  },
  {
    path: 'index',
    loadChildren: './my-partner/my-partner.module#MyPartner',
    canActivate: [AuthGuard]
  },
  {
    path: 'order-tracking',
    loadChildren: './order-tracking/order-tracking.module#OrderTrackingModule',
  },
  {
    path: 'payment',
    loadChildren: './payment/payment.module#PaymentModule',
  },
  {
    path: 'order-product',
    loadChildren: './order-product/order-product.module#OrderProductModule',
  },
  {
    path: 'cart',
    loadChildren: './cart/cart.module#CartModule',
  },
  {
    path: 'release-order',
    loadChildren: './release-order/release-order.module#ReleaseOrderModule',
  },
  {
    path: 'ars-order',
    loadChildren: './ars-order/ars-order.module#ArsOrderModule',
  },
  { path: '', redirectTo: '/index', pathMatch: 'full' },
  // {path: '', redirectTo: '/order-product/product-list', pathMatch: 'full'},
  { path: '**', redirectTo: '/', pathMatch: 'full' }
];


@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})

export class AppRoutingModule {
}



