import { Injectable } from '@angular/core';
import { Http, Response,RequestOptions,Headers ,URLSearchParams } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import { Router }  from '@angular/router';
import { UserInfo } from './userInfo'
import { CommonFunc } from '../shared/config';
import { AuthHttpService } from 'client-mypartner-lib/src/services/auth/auth-http.service';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/toPromise';
import 'rxjs/add/operator/catch';
import { environment } from 'environments/environment';

@Injectable()
export class DataForm {
    public url= '/api/mypartner/';
    public orderTracking : any = [];
    public refData : any = {};


    constructor(
        private router: Router,
        private http : Http,
        private func : CommonFunc,
        private authHttpService : AuthHttpService,
        private service : UserInfo
    ) {}
    
    getProfileInfoLoadTest(code : number) {
        let data = {'locationCode': code};
        return this.http.post('http://192.168.1.246:8081/api/test/getProfileInfo',
            data, this.getPutHeaders()).toPromise()
            .then(this.extractData)
            .catch(this.handleError);
    }

    getProfileInfo(code : number) {
        let data = {'locationCode': code,
            'temp' : this.service.gToken('', code.toString()),
            'temp_' : this.service.getAccessToken()
        };
        return this.authHttpService.post('getProfileInfo',
            data).toPromise()
            .then(this.extractData)
            .catch((error : Response | any) => {
                $("#preload").css("display", "none");
                let errMsg: string;
                if (error instanceof Response){
                    const body = error.json() || '';
                    const err = body.error || JSON.stringify(body);
                    errMsg = `${error.status} - ${error.statusText || ''} ${err}`;
                }else{
                    errMsg = error.message ? error.message : error.toString();
                }
                return Observable.throw(errMsg);
            });
    }

    getOrderTrackingDetail(){
        let data = {
        };
        return this.authHttpService.post('orderTrackingDetail',
                data).toPromise()
                .then(this.extractData)
            .catch((error : Response | any) => {
                $("#preload").css("display", "none");
                let errMsg: string;
                if (error instanceof Response){
                    const body = error.json() || '';
                    const err = body.error || JSON.stringify(body);
                    errMsg = `${error.status} - ${error.statusText || ''} ${err}`;
                }else{
                    errMsg = error.message ? error.message : error.toString();
                }
                return Observable.throw(errMsg);
            });
    }

    clearUndefined(value: string) {
        if(value === undefined) {
            return '';
        }
        return value
    }

    formatDate(value: string, end: boolean = false) {
        let response: string;
        response = value;
        if(value !== undefined && value !== '' && value.indexOf("-")) {
            let date = new Date(value);
            response =  date.getDate() + "/" + (date.getMonth()+1) + "/" + date.getFullYear() + (end === false ? (" 00:00:00") : (" 23:59:59"));
        }
        return response;
    }

    /* test Load */
    getOrderTrackingTest(){

        let data = {
        };

        return this.authHttpService.post('test/orderTracking',
            data).toPromise()
            .then(this.extractData)
            .catch(this.handleError);
    }
    /* test Load */

    getOrderTracking(params: any = {}){

        let data = {
            'reqDealerCode':params.DealerCode,
            'reqOrderNo': '',
            'sapPoNo': this.clearUndefined(params.PONo),
            'orderDateFrom': this.formatDate(this.clearUndefined(params.startDate)),
            'orderDateTo': this.formatDate(this.clearUndefined(params.endDate), true),
            'sapOrderNo': this.clearUndefined(params.SAPNo),
            'temp' : this.service.gToken(),
            'temp_' : this.service.getAccessToken()
            };

        return this.authHttpService.post('orderTracking',
            data).toPromise()
            .then(this.extractData)
            .catch(this.handleError);
    }

    private getPutHeaders(){
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    return  new RequestOptions({
            headers: headers
    });
  }

  private extractData(res){
      return res;
  }

  private handleError(error: Response | any) {
      if (typeof error === 'object'&& error.toString().indexOf("Cannot") === -1) {
          let errMsg: string;
          if (error instanceof Response) {
              const body = (error.toString().indexOf('Cannot') === -1 ?  error.json() : '') || '';
              const err = body.error || JSON.stringify(body);
              errMsg = `${error.status} - ${error.statusText || ''} ${err}`;
          } else {
              errMsg = error.message ? error.message : error.toString();
          }
          return Observable.throw(errMsg);
      }
  }

}





