import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ArsOrderMainComponent } from './ars-order-main.component';
import { ArsOrderComponent } from './ars-order.component';


let base_img = './assets/images/'

const releaseRoutes: Routes = [
    {
        path: '', component: ArsOrderMainComponent,
        children: [
            {
                path: '', component: ArsOrderComponent,
                data: {
                    backLink: '/index',
                    backLinkStatus: true,
                    active: 'AWN',
                    tabStatus: true,
                    image: base_img + 'bannerReleaseOrder1.png',
                    searchTool: true,
                    alert: {
                        show: false,
                    }
                }
            }
        ]
    }
];


@NgModule({
    imports: [RouterModule.forChild(releaseRoutes)],
    exports: [RouterModule],
    providers: []
})
export class ARSOrderRoutingModule { }



