import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { RouterModule } from '@angular/router';

import { ConstantApp } from '../constant/ConstantApp'
import { CommonFunc } from '../shared/config';
import { TopbarSharedModule } from '../shared/topbar.module';
import { modalService } from '../services/modalService'
import { ServiceShared } from '../shared/service.module'
import { DataForm } from '../services/form'
import { ReleaseOrderRoutingModule } from './release-order.routing.module';
import { ReleaseOrderComponent } from './release-order.component';
import { ReleaseOrderService } from './release-order.service';
import { OrderDetailComponent } from './order-detail/order-detail.component';
import { HeaderComponent } from './shared/header/header.component';
import { SummaryDetailComponent } from './summary-detail/summary-detail.component';
import { ReleaseDetailComponent } from './release-detail/release-detail.component';


@NgModule({
  imports: [
    CommonModule,
    TopbarSharedModule,
    ReleaseOrderRoutingModule,
    RouterModule,
    ServiceShared
  ],
  declarations: [
    ReleaseOrderComponent,
    OrderDetailComponent,
    HeaderComponent,
    SummaryDetailComponent,
    ReleaseDetailComponent

  ],
  providers: [
    ReleaseOrderService,
    CommonFunc,
    ConstantApp,
    modalService,
    DataForm
  ]
})
export class ReleaseOrderModule { }



