import { Injectable } from '@angular/core';
import { AuthHttpService } from 'client-mypartner-lib/src/services/auth/auth-http.service';
import { ArsOder } from '../models/ars-order.model';
import { Http } from '@angular/http';
import { Observable } from 'rxjs';

@Injectable()
export class ArsOrderService {

  arsOderData: ArsOder;

  constructor(private authHttpService: AuthHttpService, private http: Http) { }


  sortArsList(data, mode) {
    if (mode === 'ASC') {
      return data.sort((a, b) => {
        if (new Date(a.createTime) < new Date(b.createTime)) {
          return -1;
        } else if (new Date(a.createTime) > new Date(b.createTime)) {
          return 1;
        } else {
          return 0;
        }
      });
    } else {
      //* mode DES
      return data.sort((a, b) => {
        if (new Date(a.createTime) > new Date(b.createTime)) {
          return -1;
        } else if (new Date(a.createTime) < new Date(b.createTime)) {
          return 1;
        } else {
          return 0;
        }
      });
    }
  }

  getArsOrders(location: string): Observable<any> {
    const body = {
      distributorLocationCode: location,
      channel: "myPartner" //* e.g. “myPartner”
    }

    // return this.authHttpService.post('/api/mypartner/getArsOrder', body); //* local
    return this.authHttpService.post('getArsOrder', body); //* sit
  }

  searchArsOrder(data): Observable<any> {

    // {
    //   distributorLocationCode: "", //* Retail Location Code
    //   retailLocationCode: "", //* Distributor Location Code
    //   startDate: "", //* yyyyMMddHHmm e.g. “202012312050” ถ้าไม่กำหนดจะย้อนเรียกหลังสามวัน
    //   endDate: "", //* yyyyMMddHHmm e.g. “202012310250”
    //   orderNo: "", //* e.g. “20200227100211273” ถ้ามี orderNo จะไม่สนใจวัน
    //   status: "", //* 0 = waiting, 1 = delivered
    //   channel: "myPartner" //* e.g. “myPartner”
    // }

    // return this.authHttpService.post('/api/mypartner/getArsOrder', data); // test
    return this.authHttpService.post('getArsOrder', data); //* sit prod
  }

  exportExcel(data): Observable<any> {
    // return this.authHttpService.post('/api/mypartner/exportArs', data); // test
    return this.authHttpService.post('exportArs', data); //* sit prod
  }

}



