import { Injectable } from '@angular/core';
import 'rxjs/add/operator/toPromise';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';
import { Subject } from "rxjs";

declare let $: any;

@Injectable()
export class AlertService {
  private buttonLists: Subject<any> = new Subject<any>();
  msgList: any = new Subject<any>();
  close = new Subject<any>();

  constructor() { /**/ }

  setCustomButton(btn: any): void {
    this.buttonLists.next(btn);
  }

  getCustomButton(): Subject<any> {
    return this.buttonLists;
  }

  setMessage(msg) {
    this.msgList.next(msg);
    $('#error').foundation('open');
  }

  setpopupMessage(msg: any): void {
    $('#error .message').text(msg);
  }

  setpopupMessageHtml(msg: any): void {
    $('#error .message').html(msg);
  }

  openPopup(): void {
    $('#error').foundation('open');
  }
  closePopup(): void {
    $('#error').foundation('close');
    $('#error .message').text('');
    $('#btnSet').html('');
    this.close.next(true);
  }
}



