import { Component, OnInit } from '@angular/core';
import { FormBuilder } from '@angular/forms';
import { ValidationManager } from "ng2-validation-manager";
import { DataForm } from 'app/services/form';
import { UserInfo } from 'app/services/userInfo';
import { Term } from 'app/constant/Term';


@Component({
  selector: 'app-landing-login',
  templateUrl: './landing-login.component.html',
  styleUrls: ['./landing-login.component.scss']
})
export class LandingLoginComponent implements OnInit {

  patternNumber: string = '^[0-9]*$';
  searchCustomerForm: any
  patternPassport: string = '^[a-z0-9]*$';
  pageMode: number = 1;

  public errorLog: boolean = false;
  public errorOTP: boolean = false;
  public username: string;
  public password: string;
  public form;
  public otp: string;
  public btnDis: boolean = false;
  private checked: boolean = false;
  private checkedTerm: boolean = false;
  private btnLoc: boolean = false;
  private locationCode: string;
  public termHeader
  public termBody
  public termList
  public showModal: boolean = false
  public showModalAlert: boolean = false

  constructor(public fb: FormBuilder, private formData: DataForm, private service: UserInfo, private term: Term) {
    $("html").css('overflow-x', 'hidden');
    $("html").css('overflow-y', 'auto');
    $("#content").removeClass('disable');
    $("#sideNav").removeClass("active");
    console.log(localStorage.getItem('userInfo'));
    if (
      typeof localStorage.getItem('userInfo') !== 'undefined' &&
      localStorage.getItem('userInfo') !== null
    ) {
      let data = JSON.parse(localStorage.getItem('userInfo'));
      this.username = data.username;
      this.password = data.password;
    }
    if (
      typeof localStorage.getItem('remember') !== 'undefined' &&
      localStorage.getItem('remember') !== null
    ) {
      this.checked = JSON.parse(localStorage.getItem('remember'));
    }
    if (
      typeof localStorage.getItem('acceptTerm') !== 'undefined' &&
      localStorage.getItem('acceptTerm') !== null
    ) {
      if (JSON.parse(localStorage.getItem('acceptTerm')) === true) {
        this.checkedTerm = true;
      } else {
        this.showAlertModal()
      }
    } else {
      this.showAlertModal()
    }

    this.termHeader = term.TermHeader
    this.termBody = term.TermBody
    this.termList = term.TermList

  }

  showAlertModal() {
    this.showModalAlert = true
  }

  closeAlertModal() {
    this.showModalAlert = false
  }
  remember = () => {
    this.checked = this.checked === false;
  }
  acceptTerm = () => {
    this.checkedTerm = this.checkedTerm === false;
  }

  rememberusername = () => {
    if (this.checked) {
      let data = {
        username: this.username,
        password: this.password
      }
      localStorage.setItem('userInfo', JSON.stringify(data))
      localStorage.setItem('remember', JSON.stringify(true))
    } else {
      localStorage.removeItem('userInfo');
      localStorage.setItem('remember', JSON.stringify(false))
    }
  }
  rememberTerm = () => {
    if (this.checkedTerm) {
      localStorage.setItem('acceptTerm', JSON.stringify(true))
    } else {
      localStorage.setItem('acceptTerm', JSON.stringify(false))
    }
  }

  openTerm = () => {
    this.openModal()
  }
  openModal = () => {
    this.showModal = true
  }
  closeModal = () => {
    this.showModal = false
  }
  login(): void {
    //   if(this.checkedTerm===true){
    if (this.form.isValid()) {
      this.service.getDetailUser(
        this.username,
        this.password, this
      ).then(res => {
        if (res.resultCode === '20000') {
          this.service.setAccessUser(res);
          this.setOtp();
          this.rememberusername();
          this.rememberTerm();
        } else {
          this.setErrorLog();
        }
      });
    }
    //   }else{
    //       $("#termCheckbox").focus()
    //   }

  }

  public setErrorLog() {
    this.errorLog = true;
  }

  private setAdminAccess() {
    this.btnLoc = true;
  }

  public setOtp() {
    this.btnDis = true;
    this.errorLog = false;
    this.form.formGroup.disable()
  }

  public getDataProfile(code: number, role: string) {
    if (role.toLowerCase().indexOf('admin') > -1) {
      this.setAdminAccess();
    } else {
      let that = this;
      $("#preload").css("display", "block");
      this.formData.getProfileInfo(code).then(res => {
        $("#preload").css("display", "none");
        if (typeof res.resultCode === 'string' &&
          res.resultCode === 'S') {
          this.service.setCompanyProfile(res);
        }
      });
    }
  }

  LogOnAdmin = () => {
    $("#preload").css("display", "block");
    this.service.setLocationCode(Number(this.locationCode));
    this.formData.getProfileInfo(Number(this.locationCode)).then(res => {
      $("#preload").css("display", "none");
      if (typeof res.resultCode === 'string' && res.resultCode === 'S') {
        this.service.setCompanyProfile(res);
      }
    });
  }

  public LogOn() {
    this.errorOTP = false;
    this.service.sendOTP(this.otp, this).then(res => {
      if (typeof res.resultCode === 'string' && res.resultCode === '20000') {
        $("#preload").css("display", "block");
        this.service.getToken(res.data.token, res).then(cb => {
          $("#preload").css("display", "none");
          if (typeof cb.resultCode === 'string' && cb.resultCode === '20000') {
            this.service.setToken(cb.data, this)
          }
        })
      } else {
        this.setErrorOTP();
      }
    });
  }

  public setErrorOTP() {
    this.errorOTP = true;
  }

  ngOnInit() {
    this.form = new ValidationManager({
      'username': 'required|minLength:4|maxLength:12',
      'password': 'required|minLength:4|maxLength:12'
    });

  }

}


