import { Component, OnInit, Input } from '@angular/core';
import { Router, NavigationEnd } from "@angular/router";
import { modalService } from '../../services/modalService';

@Component({
  selector: 'app-topbar',
  templateUrl: './topbar.component.html',
  styleUrls: ['./topbar.component.scss']
})
export class TopbarComponent implements OnInit {
    @Input() headerTxt: string;
    @Input() backLink: boolean;
    private linkURL_ : string;
    @Input()
    set linkURL(linkURL: string) {
        this.linkURL_ = linkURL
    }
    @Input() cartLink: boolean;
    @Input() alert: any;

    public modalShow: boolean = false;
    public alertTxt: string = "";
    public showCancelModalButton: boolean = false;

    url : string;
    previousUrl:string;

    constructor(
        private router: Router
    ) {
        router.events
            .filter(event => event instanceof NavigationEnd)
            .subscribe(e => {
                if(typeof this.url === 'undefined') {
                    this.url = e.url
                }
                this.previousUrl = this.url
                this.url = e.url
            });
    }

    ngOnInit() {
    }

    toBackLink(){
      if(this.linkURL_ === '' && typeof this.linkURL_ !== 'undefined') {
        this.router.navigate([this.previousUrl]);
      } else {
        if(this.alert != undefined){
            if (this.alert.show == true){
                this.modalShow = true
                this.showCancelModalButton = this.alert.showCancel
                this.alertTxt = this.alert.message
            } else {
                this.router.navigate([this.linkURL_]);
            }
        } else {
            this.router.navigate([this.linkURL_]);
        }
      }
    }

    onClickCancel(event){
        this.modalShow = false
    }

    onClickSubmit(event){
        this.modalShow = false
        this.router.navigate([this.linkURL_]);
    }
    
}



