import { Injectable } from '@angular/core';import { Subject } from 'rxjs/Subject';import { Observable } from 'rxjs/Observable';@Injectable()export class modalService {    public subject : any = new Subject<any>();    modalHandle(): Observable<any> {        return this.subject.asObservable();    }    alert(agv : any) {        this.subject.next({            show : agv[0],            alertTxt : agv[1],            cancelBtn : agv[2] || false,            reDirect : agv[3] || ''        });    }    handle() {    }}


