import { Injectable } from '@angular/core';
import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, Router } from '@angular/router';
import { Observable } from 'rxjs/Observable';
import { AuthGuardService } from './auth-guard.service';
import { JwtHelper } from 'angular2-jwt';
import { CookieService } from 'angular2-cookie';

@Injectable()
export class AuthGuard implements CanActivate {

  constructor(private auth: AuthGuardService, private router: Router, private cookieService: CookieService) { }
  jwtHelper: JwtHelper = new JwtHelper();
  canActivate(
    next: ActivatedRouteSnapshot,
    state: RouterStateSnapshot): Observable<boolean> | Promise<boolean> | boolean {
    console.log(this.auth.loggedIn())

    var token = this.cookieService.get('accessToken');

    // console.log(this.jwtHelper.decodeToken(token));
    // console.log(this.jwtHelper.getTokenExpirationDate(token));
    // console.log(this.jwtHelper.isTokenExpired(token));

    if (!this.cookieService.get('accessToken')) {
      console.log('non accessToken');
      this.router.navigate(['/login']);
      return false;
    } else if (this.jwtHelper.isTokenExpired(token)) {
      console.log('isTokenExpired');
      this.router.navigate(['/login']);
      return false;
    } else {
      return true;
    }

  }
}



