import { Component, OnInit, Output, Input } from '@angular/core';
import { modalService } from '../../services/modalService'
import { ActivatedRoute, Router } from '@angular/router';
import { AuthHttpService } from 'client-mypartner-lib/src/services/auth/auth-http.service';
import { AuthService } from 'client-mypartner-lib/src/services/auth/auth.service';

@Component({
  selector: 'app-modal-alert',
  templateUrl: 'modal-alert.component.html',
  styleUrls: ['modal-alert.component.scss']
})
export class ModalAlertComponent implements OnInit {
  @Input() show : boolean  = false;
  @Input() cancelBtn : boolean  = false;
  @Input() alertTxt : string  = '';
  @Input() reDirect : any = '';


  constructor(
      private modalService : modalService,
      private router : Router,
      private route : ActivatedRoute,
      private authHttpService : AuthHttpService,
      private authService: AuthService
  ) { }

  ngOnInit() {
  }

  dismiss() : void {
    this.modalService.alert([false, '']);
  }

  closeModal(): void {
    this.modalService.alert([false, '']);
    if(this.reDirect !== '') {
      this.router.navigate([this.reDirect]);
    }
  }

  clearToken(): void {
    if(this.reDirect === "/index/login") {
      this.authService.removeTokens();
      this.authService.requireLoginSubject.next(true);
      //this.authHttpService.logout();
    }
    this.closeModal();
  }

}



