import { Component, OnInit, Input, EventEmitter, Output } from '@angular/core';
import { ArsOder } from '../models/ars-order.model';

@Component({
  selector: 'app-ars-list',
  templateUrl: './ars-list.component.html',
  styleUrls: ['./ars-list.component.scss']
})
export class ArsListComponent implements OnInit {

  @Input() arsList: ArsOder;
  @Output() selectedArsList = new EventEmitter();

  constructor() { }

  ngOnInit() {
    console.log(this.arsList);
  }

  dateConvert(date) {
    const split = date.split('-');
    return split[2].substr(0, 2) + '/' + split[1] + '/' + split[0];
  }

  onClockArsList(data) {
    this.selectedArsList.emit(data);
  }

}



