import { Component, OnInit, Output, Input, EventEmitter  } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { AuthHttpService } from 'client-mypartner-lib/src/services/auth/auth-http.service';

@Component({
  selector: 'app-modal-topbar',
  templateUrl: 'modal-topbar.component.html',
  styleUrls: ['modal-topbar.component.scss']
})
export class ModalTopbarComponent implements OnInit {
  @Input() show : boolean = false;
  @Input() cancelBtnShow : boolean  = false;
  @Input() alertTxt : string  = '';
  @Input() reDirect : any = '';
  @Output() onClickCancel = new EventEmitter<boolean>();
  @Output() onClickSubmit = new EventEmitter<boolean>();


  constructor(
      private router : Router,
      private route : ActivatedRoute,
      private authHttpService : AuthHttpService,
  ) { }

  ngOnInit() {
  }

  dismiss() : void {
    this.onClickCancel.emit(this.show)
  }

  submit(): void {
    this.onClickSubmit.emit(this.show)
  }

}



