import { Injectable } from '@angular/core';
import { Http, Response } from '@angular/http';
import { AuthHttpService } from 'client-mypartner-lib/src/services/auth/auth-http.service';
import { Observable } from 'rxjs/Observable';
import { UserInfo } from './userInfo';
import { CookieService } from "angular2-cookie/services/cookies.service";


@Injectable()
export class UserData {
    private dataProfile : {}

    constructor (
        private http: Http,
        private authHttpService : AuthHttpService,
        private service : UserInfo,
        private _cookieService: CookieService,
    ) {
        
    }  

    saveUser(): Promise<any> {
        let role = this.service.getRole()
        let body = {}
        if (role == "Owner") {
            body = {
                'sessionId': this.service.getSessionId(),
                'username' : this.service.getUsername(),
                'fullname' : this.service.getCompanyName(),
                'role': role,
                'type': "DEALER",
                'value': this.service.getDealerCode(),
                'myChannelToken': this._cookieService.get("accessToken"),
                'temp' : this.service.gToken(this.service.getUsername()),
                'temp_' : this.service.getAccessToken()
            };
        } else {
            body = {
                'sessionId': this.service.getSessionId(),
                'username' : this.service.getUsername(),
                'fullname' : this.service.getCompanyName(),
                'role': role,
                'type': "LOCATION",
                'value': this.service.getLocationCode(),
                'myChannelToken': this._cookieService.get("accessToken"),
                'temp' : this.service.gToken(this.service.getUsername()),
                'temp_' : this.service.getAccessToken()
            };
        }
        
        return this.authHttpService.post('saveUser',
            body).toPromise()
                .then(this.extractData)
                .catch((error : Response | any) => {
                    $("#preload").css("display", "none");
                    let errMsg: string;
                    if (error instanceof Response){
                        const body = error.json() || '';
                        const err = body.error || JSON.stringify(body);
                        errMsg = `${error.status} - ${error.statusText || ''} ${err}`;
                    }else{
                        errMsg = error.message ? error.message : error.toString();
                    }
                    return Observable.throw(errMsg);
        });    
    }

    private extractData(res) {
        return res;
    }

    getUserToken(): Promise<any> {
        let body = {
            'myChannelToken': this._cookieService.get("accessToken"),
            'temp' : this.service.gToken(this.service.getUsername()),
            'temp_' : this.service.getAccessToken()
        };
        
        return this.authHttpService.post('getUserToken',
            body).toPromise()
                .then(this.extractData)
                .catch((error : Response | any) => {
                    $("#preload").css("display", "none");
                    let errMsg: string;
                    if (error instanceof Response){
                        const body = error.json() || '';
                        const err = body.error || JSON.stringify(body);
                        errMsg = `${error.status} - ${error.statusText || ''} ${err}`;
                    }else{
                        errMsg = error.message ? error.message : error.toString();
                    }
                    return Observable.throw(errMsg);
        });    
    }
}



