import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule } from '@angular/router';
import { FormsModule } from '@angular/forms';

import { ConstantApp } from '../constant/ConstantApp'
import { CommonFunc } from '../shared/config';
import { TopbarSharedModule } from '../shared/topbar.module';
import { modalService } from '../services/modalService'
import { ServiceShared } from '../shared/service.module'
import { DataForm } from '../services/form'

import { ARSOrderRoutingModule } from './ars-order.routing.module';
import { MyDatePickerModule } from 'mydatepicker';

import { ArsOrderService } from './service/ars-order.service';

import { ArsOrderComponent } from './ars-order.component';
import { ArsOrderMainComponent } from './ars-order-main.component';
import { ArsHeaderComponent } from './ars-header/ars-header.component';
import { ArsListComponent } from './ars-list/ars-list.component';
import { ArsSearchComponent } from './ars-search/ars-search.component';
import { ArsDetailComponent } from './ars-detail/ars-detail.component';
import { ArsAlertModalComponent } from './ars-alert-modal/ars-alert-modal.component';




@NgModule({
  imports: [
    CommonModule,
    TopbarSharedModule,
    RouterModule,
    ARSOrderRoutingModule,
    ServiceShared,
    MyDatePickerModule,
    FormsModule
  ],
  declarations: [
    ArsOrderMainComponent,
    ArsOrderComponent,
    ArsHeaderComponent,
    ArsListComponent,
    ArsSearchComponent,
    ArsDetailComponent,
    ArsAlertModalComponent
  ],
  providers: [
    CommonFunc,
    ConstantApp,
    modalService,
    DataForm,
    ArsOrderService
  ]
})
export class ArsOrderModule { }



