import { Component, Input, OnInit } from '@angular/core';
import { AlertService } from "./alert.service";

@Component({
  selector: 'mc-alert',
  templateUrl: 'alert.component.html',
  styleUrls: ['alert.component.scss']
})
export class AlertComponent implements OnInit {
  customButton: any;
  rowLength: number;
  textList: any;

  constructor(private alertService: AlertService) { }

  ngOnInit(): void {
    this.alertService.getCustomButton().subscribe(buttonLists => {
      console.log(buttonLists)
      this.customButton = buttonLists;
      this.rowLength = buttonLists.length;
    });

    this.alertService.msgList.subscribe(val => {
      console.log(val)
      if (val && val.length > 0) {
        this.textList = val;
      }
    })
  }

  close(): void {
    this.alertService.closePopup();
    this.alertService.setCustomButton([]);
    this.alertService.setpopupMessage('');
  }

}



