import { Component, OnInit, Input, EventEmitter, Output } from '@angular/core';


@Component({
  selector: 'app-ars-detail',
  templateUrl: './ars-detail.component.html',
  styleUrls: ['./ars-detail.component.scss']
})
export class ArsDetailComponent implements OnInit {

  @Input() selectedArs: any;
  @Output() close = new EventEmitter();
  @Output() exportExcel = new EventEmitter();

  constructor() { }

  ngOnInit() {
    console.log(this.selectedArs);
  }

  toBackLink() {
    this.close.emit();
  }

  dateConvert(date) {
    const split = date.split('-');
    return split[2].substr(0, 2) + '/' + split[1] + '/' + split[0];
  }

  onClickExport() {
    this.exportExcel.emit(this.selectedArs);
  }

  onClickPrint() {
    console.log('on click print');
  }

}



