import { Component, OnInit, OnDestroy } from '@angular/core';
import { Location } from '@angular/common';

import { ReleaseOrderService } from '../release-order.service';
import { ListAutoUnblock, QueryAccBlock, ListBlockOrder, ReleaseOrderResponse, ListBlockedOrderResponse } from 'app/model/release-order.model';
import { Router } from '@angular/router';
import { modalService } from '../../services/modalService';
import { AlertService } from 'app/shared/alert/alert.service';
import { PageLoadingService } from 'client-mypartner-lib/src/components/page-loading/page-loading.service';
import { UserInfo } from 'app/services/userInfo';


@Component({
  selector: 'app-summary-detail',
  templateUrl: './summary-detail.component.html',
  styleUrls: ['./summary-detail.component.scss']
})
export class SummaryDetailComponent implements OnInit, OnDestroy {
  releaseData: any;
  heightContent = '';
  myStyle = (window.screen.height / 2).toString() + 'px;';

  constructor(private user: UserInfo, private _location: Location, private pageLoadingService: PageLoadingService, private alertService: AlertService, private modalService: modalService, private router: Router, private releaseOrderService: ReleaseOrderService) { }

  ngOnInit() {
    this.setHeightContent();
    console.log(this.releaseOrderService.releaseSelected);
    this.releaseData = this.releaseOrderService.releaseSelected ? this.releaseOrderService.releaseSelected : { listBlockOrderCredit: [], overDue: null };
    this.releaseOrderService.page.next('summary');
    this.mapReleaseData();
  }

  setHeightContent() {
    if (window.screen.height >= 1366) {
      this.heightContent = '850px;'
    } else if (window.screen.height >= 1024) {
      this.heightContent = '570px;'
    } else {
      this.heightContent = (window.screen.height / 2).toString() + 'px;'
    }
  }

  mapReleaseData() {
    this.releaseData.listBlockOrderCredit.map(val => {
      val.listAutoUnblockOrderDetailBean.map(res => {
        res.collapse = false
      })
    })
  }

  collapseSwitch(check: boolean) {
    if (check) {
      return './assets/images/icon/icon-arrow-bottom-green.png';
    } else {
      return './assets/images/icon/icon-arrow-top-green.png';
    }
  }

  onCollapseSwitch(creditIdx: number, listIdx: number) {
    this.releaseData.listBlockOrderCredit[creditIdx].listAutoUnblockOrderDetailBean[listIdx].collapse = !this.releaseData.listBlockOrderCredit[creditIdx].listAutoUnblockOrderDetailBean[listIdx].collapse;
  }

  onCancel() {
    // this.router.navigate(['/release-order']);
    this._location.back();
  }

  onNext() {
    // connect Release order API
    this.pageLoadingService.openLoading();
    // const amount = parseFloat(this.user.getList()[0].val.replace(/,/g, ''));
    console.log(this.releaseData.overDue);
    if (this.releaseData.overDue) {
      let text = '';
      this.releaseData.overDue ? text += 'Overdue Trading' : '';
      // amount <= 0
      //   ? text.length > 0
      //     ? text += ' และ ยอดเงินคงเหลือไม่พอ' : 'ยอดเงินคงเหลือไม่พอ' 
      //   : '';

      this.modalService.alert([true, text]);
      this.pageLoadingService.closeLoading();
    } else {
      this.releaseOrderService.postReleaseOrder(this.releaseData).then((res: ReleaseOrderResponse) => {
        console.log(res);
        if (res && res.resultCode === "S") {
          console.log(res);
          this.releaseOrderSuccess(res.listBlockedCreditOrder)
          this.pageLoadingService.closeLoading();
        } else {
          console.log(res);
          this.modalService.alert([true, 'Release Order Fail']);
          this.pageLoadingService.closeLoading();
        }
      }).catch(err => {
        console.log(err)
        this.modalService.alert([true, 'Error']);
        this.pageLoadingService.closeLoading();
      });
    }
  }

  releaseOrderSuccess(data: ListBlockedOrderResponse[]) {
    console.log('Release success data : ', data)
    const arr = [];
    this.releaseOrderService.releaseSelected.listBlockOrderCredit = data;
    this.alertService.setCustomButton([{ name: 'CLOSE', function: 'close', class: 'close-release' }]);
    data.forEach(res => {
      res.listAutoUnblockOrderDetailBean.forEach((val: ListAutoUnblock, idx: number) => {
        arr.push({ text: `เลขที่คำสั่งซื้อ : ${val.sapOrderNo} : ${val.status}`, status: val.status })
      });
    })
    this.alertService.setMessage(arr);
    this.alertService.close.subscribe(val => {
      if (val) {
        this.router.navigate(['/release-order/detail']);
      }
    });
  }

  ngOnDestroy() {
    this.alertService.close.next(null);
  }

}



