import { Component, OnInit, Input } from '@angular/core';
import { Router, NavigationEnd } from "@angular/router";
import { modalService } from '../../../services/modalService'
import { ReleaseOrderService } from 'app/release-order/release-order.service';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss']
})
export class HeaderComponent implements OnInit {
  @Input() backLinkStatus: boolean;
  private linkURL_: string;
  @Input()
  set linkURL(linkURL: string) {
    this.linkURL_ = linkURL
  }
  @Input() active: string;
  @Input() tab: boolean;
  @Input() image: string = '';
  @Input() searchTool: boolean = false;
  private searchBox: boolean = false
  page: string;
  awnLength: number = 0;
  wdsLength: number = 0;

  constructor(
    private releaseOrderService: ReleaseOrderService,
    private router: Router,
    private modalSV: modalService,
    // private PMService : PaymentService,
    // private PMCService : PaymentServiceCredit,
    // private bank : BankAccount
  ) { }

  onSearchClick() {
    if (this.searchBox === true) {
      // this.PMService.onSerach('')
      // this.PMCService.onSerach('')
    }
    this.searchBox = this.searchBox === false
  }

  onSearch(e) {
    // this.PMService.onSerach(e.target.value)
    // this.PMCService.onSerach(e.target.value)
  }

  ngOnInit() {
    this.releaseOrderService.page.subscribe(res => {
      this.page = res;
    })

    this.releaseOrderService.company.subscribe(val => {
      if (val === 'AWN') {
        this.wdsLength = this.releaseOrderService.wdsCount;
        this.awnLength = this.releaseOrderService.awnCount;
      }
    })

  }


  onChangeMenu(company: string): void {
    if (this.page === 'detail') {
      console.log(company);
      this.active = company;
      this.releaseOrderService.company.next(this.active)
    }
  }

  toBackLink() {
    this.router.navigate([this.linkURL_]);
  }

}



