export class Term {
  public TermHeader: string = 'ข้อตกลงและเงื่อนไขการใช้ myPartner'
  public TermBody = [
    'ขอต้อนรับและขอขอบคุณท่านในการใช้แอพพลิเคชั่น myPartner ผ่านระบบคอมพิวเตอร์ที่ช่วยอำนวยความสะดวกให้แก่ท่านในฐานะเป็นตัวแทนจำหน่ายสินค้า/บริการของบริษัท ในการใช้บริการต่างๆ และหรือทำธุรกรรมอิเล็กทรอนิกส์ต่างๆ อันเกี่ยวกับการสั่งซื้อและ/หรือชำระเงินค่าสินค้า (Order&Payment) การตรวจสอบผลการดำเนินงาน (Performance Report) การเรียกดูหรือตรวจสอบรายการผลตอบแทนจากการให้บริการ (Rebate Report) การรับแจ้งหรือตรวจสอบข้อมูลข่าวสารการส่งเสริมการขาย (Channel Communication) และบริการหรือรายการอื่นเพิ่มเติมในภายหน้า',
    'หากมีผู้ใดกระทำการใดๆ ให้เกิดความเสียหายแก่ระบบคอมพิวเตอร์หรือบุคคลใด ผ่านแอพพลิเคชั่น myPartner นี้ ผู้กระทำการดังกล่าวจะต้องรับผิดทั้งทางแพ่งและทางอาญาตามที่กฎหมายบัญญัติไว้'
  ]
  public TermList = [
    '1. คำนิยาม',
    '(1)   ระบบคอมพิวเตอร์ หมายถึง อุปกรณ์หรือชุดอุปกรณ์ของคอมพิวเตอร์ที่เชื่อมการทำงานเข้าด้วยกัน โดยได้มีการกำหนดคำสั่ง ชุดคำสั่ง หรือสิ่งอื่นใด และแนวทางปฏิบัติงานให้อุปกรณ์หรือชุดอุปกรณ์ทำหน้าที่ประมวลผลข้อมูลอัตโนมัติที่ใช้ประกอบการจัดทำ myPartner ',
    '(2)   “ผู้ใช้” หมายถึง บุคคลหรือนิติบุคคลที่ได้ตกลงหรือทำสัญญากับบริษัทในการเป็นตัวแทนจำหน่ายสินค้า/บริการของบริษัท ซึ่งได้ใช้ username  password และ OTP ที่บริษัทสร้างให้เพื่อใช้ทำรายการต่างๆ บน myPartner และให้หมายความรวมถึงผู้ที่เข้าใช้งาน myPartner ด้วย username และ password และ OTP ของผู้รับสิทธิ เพื่อทำรายการต่างๆ บน myPartner ',
    '(3) “บริษัท”  หมายถึง บริษัท แอดวานซ์ ไวร์เลส เน็ทเวอร์ค จำกัด ',
    '(4)  “myPartner”  หมายถึง แอพพลิเคชั่นที่พัฒนาขึ้นสำหรับช่วยอำนวยความสะดวกให้แก่ผู้ใช้  เพื่อใช้บริการต่างๆ และหรือทำธุรกรรมอิเล็กทรอนิกส์ต่างๆ อันเกี่ยวกับการสั่งซื้อและ/หรือชำระเงินค่าสินค้า (Order&Payment) การตรวจสอบผลการดำเนินงาน (Performance Report) การเรียกดูหรือตรวจสอบรายการผลตอบแทนจากการให้บริการ (Rebate Report) การรับแจ้งหรือตรวจสอบข้อมูลข่าวสารการส่งเสริมการขาย (Channel Communication) และบริการหรือรายการอื่นเพิ่มเติมในภายหน้า ',
    '(5) username &  password หมายถึง  username &  password  ที่บริษัทสร้างให้เพื่อใช้ยืนยันตัวตน สำหรับการเป็นผู้ใช้ myPartner ',
    '(6)   OTP : One Time Password หมายถึง รหัสผ่านแบบใช้ได้เพียงครั้งเดียว ปรากฏในรูปอักษร อักขระ ตัวเลข หรือสัญลักษณ์อื่นใดที่สร้างขึ้นและจัดส่งให้แก่ผู้ใช้ผ่านบริการ SMS ไปยังหมายเลขโทรศัพท์เคลื่อนที่ผู้ใช้ได้แจ้งไว้ สำหรับนำมาใช้แสดงความสัมพันธ์กับ username &  password และหมายเลขโทรศัพท์ของผู้ใช้บริการรายนั้น และเพื่อยืนยันว่าการทำธุรกรรมทางอิเล็กทรอนิกส์รายการต่างๆ ในระบบ myPartner ผู้ใช้เป็นผู้กระทำ หรือเป็นผู้ลงลายมือชื่ออิเล็กทรอนิกส์กำกับการทำธุรกรรมทางอิเล็กทรอนิกส์นั้นเอง ',
    '2. เมื่อผู้ใช้ได้กรอกข้อมูล และได้ปฏิบัติตามขั้นตอนที่กำหนดไว้ผ่าน myPartner เรียบร้อยแล้ว ถือว่าผู้ใช้บริการตกลงยอมรับและปฏิบัติตามข้อตกลงฯ ที่กำหนดไว้นี้แล้ว ',
    '3. บริษัทมีสิทธิพิจารณาอนุญาตและยกเลิกการใช้บริการ myPartner ของผู้ใช้  และ/หรือแก้ไขเพิ่มเติมข้อตกลงฯ นี้ โดยไม่จำเป็นต้องแจ้งให้ทราบล่วงหน้า  และหากจำเป็นต้องมีการแก้ไข เพิ่มเติมเงื่อนไขใดๆ ของข้อตกลงฯ นี้ ผู้ใช้จะได้รับแจ้งเป็นลายลักษณ์อักษรผ่าน myPartner  และให้ถือว่าการแก้ไขเพิ่มเติมดังกล่าว เป็นส่วนหนึ่งของข้อตกลงฯ นี้ และท่านตกลงยอมรับเงื่อนไขดังกล่าวทุกประการ ',
    '4. แอพพลิเคชั่น myPartner ถูกควบคุมในประเทศไทย หากผู้ใช้เข้าสู่ระบบคอมพิเตอร์หรือใช้ myPartner นอกราชอาณาจักรไทย ผู้ใช้ตกลงและยินยอมให้บังคับตามกฎหมายไทย ',
    '5. ข้อมูลที่ปรากฏบน myPartner นี้ รวมถึงเครื่องหมายการค้า เครื่องหมายบริการ เป็นต้น เป็นทรัพย์สินทางปัญญาของบริษัทและที่บริษัทได้รับอนุญาตให้ใช้ ',
    '6. ผู้ใช้รับรองว่าข้อมูลต่างๆ ที่ได้กรอกเพื่อเข้าใช้ myPartner และเข้าทำธุรกรรมอิเล็กทรอนิกส์รายการต่างๆ บน myPartner เป็นข้อมูลที่ถูกต้องตรงตามความเป็นจริงทุกประการ และสามารถใช้ เป็นหลักฐานได้ หากมีการตรวจพบว่าข้อมูลดังกล่าวเป็นข้อมูลเท็จ ไม่ถูกต้อง บริษัทมีสิทธิระงับ ยกเลิกการใช้บริการ myPartner ได้ทันที ',
    '7. ผู้ใช้ยอมรับว่าข้อมูลส่วนบุคคลที่ได้กรอกเพื่อใช้งานบน myPartner รวมถึงการทำธุรกรรมอิเล็กทรอนิกส์รายการต่างๆ ในบน myPartner จะถูกส่งผ่านอินเทอร์เน็ตซึ่งเป็นเครือข่ายสาธารณะ โดยบริษัทมีการกำหนดมาตรการมิให้บุคคลภายนอกสามารถเข้าถึงข้อมูลส่วนบุคคลดังกล่าว อย่างไรก็ตามบริษัทจะไม่รับผิดชอบต่อความเสียหายใดๆ ที่เกิดขึ้นจากการที่บุคคลภายนอกได้เห็น และ/หรือใช้ข้อมูลดังกล่าวอันเกิดขึ้นจากผู้ใช้เอง ',
    '8.ผู้ใช้ตกลงใช้ myPartner เพื่อทำรายการ และ/หรือทำธุรกรรมอิเล็กทรอนิกส์รายการต่างๆ ตามที่บริษัทกำหนดไว้ตามข้อตกลงฯ นี้ เท่านั้น โดยผู้ใช้ต้องไม่กระทำการใดๆ ที่ไม่เหมาะสม หรือผิดกฎหมาย หรือขัดต่อหลักเกณฑ์ ระเบียบ ข้อบังคับ หรือคำสั่งใดๆ ที่ใช้บังคับอยู่ในขณะนั้น  หากฝ่าฝืนเงื่อนไขข้อนี้ บริษัทมีสิทธิระงับการใช้ myPartner และบอกเลิกสัญญาที่ท่านได้ทำไว้กับบริษัทได้ทันที ',
    '9.ผู้ใช้รับทราบและยอมรับว่าในการใช้ myPartner เกี่ยวกับการหักชำระค่าสินค้าและหรือบริการให้แก่บริษัท โดยหักเงินจากบัญชีเงินฝากธนาคารนั้น  myPartner เป็นเพียงช่องทางเชื่อมต่อไปยังระบบให้บริการหรือเว็บไซต์ของธนาคารเท่านั้น การกระทำใดๆ เพื่อมีผลให้ต้องมีการหักเงินหรือเรียกเก็บเงินจากบัญชีเงินฝากธนาคาร เป็นข้อตกลงระหว่างเจ้าของบัญชีเงินฝากธนาคารกับธนาคารนั้นๆ เอง ',
    '10.ข้อตกลงฯ นี้ เป็นส่วนเพิ่มเติมและใช้ร่วมกับเงื่อนไข ข้อตกลง และหรือสัญญาตัวแทนจำหน่ายสินค้า/บริการ ที่ผู้ใช้ได้ทำไว้กับบริษัท ดังนั้น นอกเหนือจากข้อตกลงฯ นี้แล้ว การทำธุรกรรมอิเล็กทรอนิกส์ใดๆ ของผู้ใช้โดยใช้ผ่าน myPartner นี้ ซึ่งเชื่อมต่ออยู่กับระบบบริการอื่นใดของบริษัท หรือการกระทำใดในฐานะเป็นตัวแทนจำหน่ายของบริษัท ต้องอยู่ภายใต้บังคับของข้อตกลง เงื่อนไข และหรือสัญญาดังกล่าวที่ผู้ใช้ได้ทำไว้กับบริษัทแล้วแต่กรณีด้วย ',
    '11. การทำธุรกรรมใด ๆ โดยใช้ username password และหรือ OTP บน myPartner ไม่ว่าจะเป็นการกระทำโดยผู้ใช้เอง หรือผู้รับมอบอำนาจจากผู้ใช้ หรือผู้ที่ผู้ใช้อนุญาตให้นำไปใช้ หรือผู้ที่แอบอ้างนำ  username password และหรือ OTP ไปใช้ ไม่ว่าด้วยความตั้งใจหรือความประมาทเลินเล่อของผู้ใช้ ให้ถือว่าการทำธุรกรรมดังกล่าวถูกต้องสมบูรณ์มีผลผูกพันผู้ใช้ หากเป็นการทำธุรกรรมที่เกี่ยวกับการชำระเงินค่าสินค้าและหรือบริการ ตลอดจนมีความเสียหายเกิดขึ้นจากการทำธุรกรรมดังกล่าวไม่ว่ากรณี ใด ผู้ใช้ต้องรับผิดชอบในการทำธุรกรรมดังกล่าวทุกประการ โดยไม่สามารถโต้แย้งหรือเรียกร้องให้บริษัทต้องรับผิดชดใช้ค่าเสียหายแต่อย่างใด ',
    '12. ผู้ใช้มีหน้าที่ต้องระมัดวังมิให้ผู้อื่นใช้ username password และหรือ OTP ของตนเองในการเข้าทำรายการผ่าน myPartner  ',
    '13. หน้าที่และความรับผิดชอบของผู้ใช้ ',
    '13.1 เรียนรู้วิธีการ และขั้นตอนการเข้าถึงและทำธุรกรรมต่างๆ ผ่าน myPartner ให้เกิดความเข้าใจอย่างถูกต้อง ชัดเจน รวมทั้งปฏิบัติตามข้อตกลงฯ นี้ ตลอดจนเงื่อนไขที่จะได้มีการแก้ไขและเพิ่มเติมขึ้นใหม่ในอนาคตอย่างเคร่งครัด ',
    '13.2 ดูแล รับผิดชอบ รวมทั้งชี้แจงให้ตัวแทน และผู้ที่ถือว่าอยู่ในความรับผิดชอบของผู้ใช้ รับทราบและปฏิบัติตามข้อตกลงฯ นี้อย่างเคร่งครัด ',
    '13.3 ให้ความร่วมมือกับบริษัท หรือผู้ที่บริษัทมอบหมายอย่างเต็มที่ในการป้องกันการใช้ระบบคอมพิวเตอร์ เพื่อการใช้งาน myPartner มิให้เกิดความเสียหาย ',
    '13.4 แจ้งให้บริษัททราบทันทีที่พบเห็นการใช้อย่างไม่ถูกต้องเหมาะสม หรือพบเห็นการเข้าถึง myPartner  รวมทั้งข้อมูลคอมพิวเตอร์โดยมิชอบ หรือรบกวนระบบคอมพิวเตอร์ ',
    '13.5 ในการใช้งานเครื่องคอมพิวเตอร์หรืออุปกรณ์ใดๆ เพื่อเข้าถึง myPartner ต้องปฏิบัติดังนี้ ',
    '       (1)   ต้องกำหนดรหัสผ่าน (Password) เพื่อล็อกหน้าจอ (Lock Screen) หากไม่ใช้งานหรือไปทำกิจกรรมอย่างอื่น เพื่อป้องกันมิให้ผู้อื่นลักลอบเข้าใช้งาน ',
    '       (2)   ต้องตรวจสอบและระมัดระวังในการนำข้อมูลคอมพิวเตอร์มา ป้อนลงในระบบคอมพิวเตอร์ทุกครั้ง และต้องติดตั้งโปรแกรมป้องกันไวรัส (Anti Virus) ที่ทันสมัยอยู่ตลอดเวลา ',
    '       (3)   ต้องปิดเครื่องคอมพิวเตอร์ อุปกรณ์ใดๆ และอุปกรณ์ต่อพ่วงทันทีเมื่อไม่ได้ใช้งานเป็นเวลานาน หรือเลิกงาน ',
    '13.6 เก็บรักษา OTP/Password ไว้เป็นความลับ มิให้ผู้อื่นล่วงรู้ ห้ามใช้รหัสร่วมกับผู้อื่น ต้องตั้ง Password ด้วยความรอบคอบ ห้ามตั้งซ้ำกับรหัสเดิม หรือที่ผู้อื่นคาดเดาได้ง่าย หรือตั้งรหัสซ้ำกัน ในทุกขั้นตอนการใช้งานเครื่องคอมพิวเตอร์ หรือทุกระบบคอมพิวเตอร์ที่มีสิทธิใช้งาน และต้องเปลี่ยน Password ตามระยะเวลาเหมาะสม หรือมีเหตุการณ์ที่สมควรต้องเปลี่ยน Password ',
    '13.7 ตกลงให้หรือดำเนินการให้บริษัท หรือผู้ที่บริษัทมอบหมายสามารถเข้าตรวจสอบการใช้งานระบบคอมพิวเตอร์ ข้อมูลคอมพิวเตอร์ สำหรับการใช้ myPartner ณ สถานที่ที่ใช้เป็นที่ติดตั้งอุปกรณ์หรือชุดอุปกรณ์เชื่อมต่อกับระบบคอมพิวเตอร์ได้ตลอดระยะเวลา ',
    '14.   ต้องดูแล รับผิดชอบมิให้ ตัวแทน และผู้ที่ถือว่าอยู่ในความรับผิดชอบของผู้ใช้กระทำการที่ถือเป็นความผิดตามที่ระบุไว้ท้ายนี้ หากมีการละเลย ฝ่าฝืน ผู้ใช้ต้องรับผิดชอบร่วมกับผู้กระทำความผิดนั้นตามกฎหมายที่เกี่ยวข้องทั้งทางแพ่งและอาญา ',
    '       (1)   เปลี่ยนแปลง แก้ไขข้อมูลโดยไม่ได้รับอนุญาตจากบริษัท ',
    '       (2)   ลักลอบนำข้อมูลไปเปิดเผย จำหน่าย จ่าย แจก แก่ผู้อื่น ไม่ว่าเพื่อประโยชน์ส่วนตัวหรือเพื่อผู้อื่นโดยไม่ได้รับอนุญาต หรือเป็นเหตุให้บริษัทได้รับความเสียหาย ',
    '       (3)   ประมาท เลินเล่อ ไม่ระมัดระวังการใช้งาน myPartner จนเป็นเหตุให้บุคคลอื่นสามารถลักลอบนำข้อมูลไปเปิดเผย จำหน่าย จ่าย แจก ',
    '       (4)   ใช้รหัสของผู้อื่นเข้าถึง myPartner เพื่อทำการอ่าน คัดลอก แก้ไข เปลี่ยนแปลง ลบทิ้ง ทำลายข้อมูล หรือทำธุรกรรมใดๆ ไม่ว่าเพื่อประโยชน์ส่วนตัวหรือเพื่อผู้อื่น ',
    '       (5)   ก่อกวน หรือทำลายข้อมูลเพื่อสร้างความเสียหายแก่บริษัทหรือผู้อื่น ',
    '       (6)   ลักลอบ เฝ้าดู ค้นหาเส้นทางหรือถอดรหัส ข้อมูลอิเล็กทรอนิกส์ โดยใช้เครื่องมือ หรือเทคโนโลยีอื่นใด เพื่อให้ได้มาซึ่งข้อมูลหรือความลับของบริษัทหรือผู้อื่น โดยจงใจก่อให้เกิดความเสียหายทั้งต่อบริษัทหรือผู้อื่น ',
    '       (7)   ให้ความช่วยเหลือหรือร่วมมือกับผู้อื่น เพื่อเข้าถึง myPartner หรือข้อมูลใด เพื่อทำการอ่าน คัดลอก แก้ไข เปลี่ยนแปลง ลบทิ้ง ทำลายข้อมูล หรือทำธุรกรรมใดๆ ไม่ว่าเพื่อประโยชน์ส่วนตัวหรือผู้อื่น ',
    '       (8)   ลักลอบใช้รหัสในการเข้าถึง myPartner และเป็นเหตุให้ผู้ให้บริการหรือผู้อื่นได้รับความเสียหาย ',
    '       (9)   ประมาท เลินเล่อ ไม่ระมัดระวังการใช้รหัส หรือยินยอมให้บุคคลอื่นใช้รหัสของตนเองเข้าถึง myPartner ',
    '       (10)  พยายามเข้าถึง myPartner ในส่วนที่ไม่มีสิทธิ์ หรือไม่ได้รับอนุญาตให้ใช้งาน ',
    '       (11)  ติดตั้ง มีไว้ในครอบครอง หรือใช้งาน Software ประเภท Hacking Tools ',
    '       (12)  เชื่อมต่ออุปกรณ์คอมพิวเตอร์ หรืออุปกรณ์อิเล็กทรอนิกส์อื่นใดเข้ากับระบบคอมพิวเตอร์โดยไม่ได้รับอนุญาตจากผู้ให้บริการ ',
    '       (13)  ลักลอบเข้าถึงระบบคอมพิวเตอร์อื่นใดของบริษั ไม่ว่าด้วยวิธีการใดๆ โดยมิชอบ ',
    '       (14)  กระทำความผิดใดๆ ตามฐานความผิดที่ระบุไว้ใน พระราชบัญญัติว่าด้วยการกระทำความผิดเกี่ยวกับคอมพิวเตอร์ พ.ศ. 2550 และกฎหมายอื่นที่เกี่ยวข้อง ',
    '16.  ผู้ใช้ต้องเป็นผู้จัดหาและรับผิดชอบค่าใช้จ่ายในการเชื่อมต่ออินเทอร์เน็ต (Internet Connection) เพื่อเข้าสู่ระบบ myPartner ตามข้อตกลงฯ นี้เอง ',
    '17.  ผู้ใช้ต้องมีอุปกรณ์ที่จำเป็นสำหรับใช้ในการเชื่อมต่อ อินเทอร์เน็ตกับ myPartner เช่น เครื่องคอมพิวเตอร์  ที่มีคุณสมบัติไม่ต่ำกว่าที่ผู้ให้บริการกำหนด หรือเครื่องโทรศัพท์เคลื่อนที่ที่รองรับการใช้งาน เป็นต้น ',
    '18.  ผู้ใช้ตกลงและยอมรับว่าบริษัทจะไม่รับผิดชอบต่อความสูญหายหรือเสียหายใดๆ อันเกิดจากการที่ผู้ใช้ใช้ MyPartner นี้ ไม่ว่าทางตรงหรือทางอ้อม ',
    '19.  กรณีมีเงื่อนไขกรณีหนึ่งกรณีใดในข้อตกลงนี้ขัดต่อกฎหมายหรือ ไม่มีผลใช้บังคับ ให้ถือว่าเงื่อนไขนั้นๆ เป็นโมฆะเฉพาะส่วนที่ขัดต่อกฎหมายหรือไม่มีผลใช้บังคับเท่านั้น โดยไม่มีผลกระทบต่อเงื่อนไขอื่นๆ ที่ยังคงความสมบูรณ์อยู่ ',
    '21.  หากผู้ใช้ไม่ปฏิบัติตามข้อตกลงและเงื่อนไขการใช้ myPartner หรือข้อตกลงใดๆ ที่ทำไว้กับบริษัท ผู้ใช้ยินยอมให้บริษัท ระงับและ/หรือยกเลิกการใช้ MyPartner ได้โดยไม่ต้องแจ้งให้ผู้ใช้ทราบล่วงหน้า ',
    'คำแนะนำ',
    '       โปรดอ่านและทำความเข้าใจข้อตกลงและเงื่อนไขการใช้ myPartner นี้อย่างละเอียด การที่ท่านลงทะเบียนเพื่อขอใช้ myPartner ตามขั้นตอนที่ผู้ให้บริการ กำหนดไว้ ถือว่าท่านตกลงยอมรับ เงื่อนไข ที่กำหนดไว้ในข้อตกลงฯ ข้างต้นทุกประการ หากท่านไม่ยอมรับข้อตกลงฯ ที่กำหนดไว้นี้ ขอให้ปฏิเสธการลงทะเบียนขอใช้ myPartner นี้'
  ]
}



