import { NgModule,ModuleWithProviders } from '@angular/core';
import { UserInfo } from '../services/userInfo'
import { CommonFunc } from '../shared/config'
import { ConstantApp } from '../constant/ConstantApp'
import { JwtHelper } from 'angular2-jwt'
import { AuthHttp} from 'angular2-jwt'
import { AuthService } from 'client-mypartner-lib/src/services/auth/auth.service';
import { AuthHttpService } from 'client-mypartner-lib/src/services/auth/auth-http.service';
import { SharedService } from 'client-mypartner-lib/src/services/shared.service';
import { DistChannel } from '../services/distChannel.service';
import { CusGroup } from '../services/cusGroup.service';
import { UserData } from '../services/userData';

@NgModule({
})
export class ServiceShared {
    static forRoot(): ModuleWithProviders {
        return {
            ngModule: ServiceShared,
            providers: [
                UserInfo,
                UserData,
                CommonFunc,
                ConstantApp,
                SharedService,
                DistChannel,
                CusGroup,
            ]
        }
    }
}



