import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { ReleaseOrderComponent } from './release-order.component';
import { OrderDetailComponent } from './order-detail/order-detail.component';
import { SummaryDetailComponent } from './summary-detail/summary-detail.component';
import { ReleaseDetailComponent } from './release-detail/release-detail.component';

let base_img = './assets/images/'

const releaseRoutes: Routes = [
    {
        path: '', component: ReleaseOrderComponent,
        children: [
            {
                path: '', component: OrderDetailComponent,
                data: {
                    backLink: '/index',
                    backLinkStatus: true,
                    active: 'AWN',
                    tabStatus: true,
                    image: base_img + 'bannerReleaseOrder1.png',
                    searchTool: true,
                    alert: {
                        show: false,
                    }
                }
            },
            {
                path: 'summary', component: SummaryDetailComponent,
                data: {
                    backLink: '/release-order',
                    backLinkStatus: true,
                    active: 'AWN',
                    tabStatus: true,
                    image: base_img + 'bannerReleaseOrder2.png',
                    searchTool: true,
                    alert: {
                        show: false,
                    }
                }
            },

            {
                path: 'detail', component: ReleaseDetailComponent,
                data: {
                    backLink: '',
                    backLinkStatus: true,
                    active: 'AWN',
                    tabStatus: true,
                    image: base_img + 'bannerReleaseOrder3.png',
                    searchTool: true,
                    alert: {
                        show: false,
                    }
                }
            }
        ]
    }
];


@NgModule({
    imports: [RouterModule.forChild(releaseRoutes)],
    exports: [RouterModule],
    providers: []
})
export class ReleaseOrderRoutingModule { }



