    public getHeaderOTP(otp: string) {
        return {
            "authorization": 'Bearer ' + this.accessToken,
            "xSsoidsSmsotpAuth": btoa(this.transactionID + ":" + otp)
        }
    }

    gToken(userName: string = '', locationCode: string = '') {
        let n = new Date();
        let nt = n.getTime() + Number(this.d);
        let b = new Date(nt);

        let k = CryptoJS.AES.encrypt(b.getFullYear() + "/" + (b.getMonth() + 1) + "/" + b.getDate() + " " + b.getHours() + ":" + b.getMinutes() + ":" + b.getSeconds(),
            userName + locationCode + this.p + this.accessToken);
        k = k.toString();
        return k;
    }


    setCompany(txt: string): void {
        this.subject.next(txt)
    }

