import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { Subject } from 'rxjs';
import { ListAutoUnblock, ReleaseOrder, QueryAccBlock } from 'app/model/release-order.model';
import { modalService } from '../services/modalService';
import { AuthHttpService } from 'client-mypartner-lib/src/services/auth/auth-http.service';
import { UserInfo } from 'app/services/userInfo';
import { PageLoadingService } from 'client-mypartner-lib/src/components/page-loading/page-loading.service';
import { Router } from '@angular/router';


@Injectable()
export class ReleaseOrderService {
  private subject: any = new Subject<any>();
  public page = new Subject<any>();
  public company = new Subject<any>();
  public releaseSelected = { listBlockOrderCredit: [], overDue: false };
  public wdsData = { listBlockOrderCredit: [], overDue: false };
  public awnData = { listBlockOrderCredit: [], overDue: false };
  public awnCount = 0;
  public wdsCount = 0;

  constructor(private router: Router, private pageLoadingService: PageLoadingService, private http: Http, private modalService: modalService, private authHttpService: AuthHttpService, private user: UserInfo
  ) { }

  getMockAWNData() {
    return this.http.get("./assets/awn.json").map((res: any) => res.json())
  }

  getMockWDSData() {
    return this.http.get("./assets/wds.json").map((res: any) => res.json())
  }

  public onSerach(val: string): void {
    this.subject.next(val);
  }

  onReleaseSelected(data) {
    this.releaseSelected = data;
    console.log(this.releaseSelected);
  }

  postReleaseOrder(data) {
    const result: ReleaseOrder = {
      userId: this.user.getUsername(),
      dealerCode: this.user.getDealerCode(),
      listUnBlockOrder: data.listBlockOrderCredit
    }
    console.log(result);
    return this.authHttpService.post("releaseOrder", result).toPromise();
  }

  getQueryAccBlock() {
    this.pageLoadingService.openLoading();
    const data = {
      dealerCode: this.user.getDealerCode(),
      listCompany: ["1201", "1401"],
      listPaymentMethod: ["C"] // S - cash , C - credit
    }

    this.authHttpService.post("queryAccBlock", data).toPromise().then((response: QueryAccBlock) => {
      console.log(response);
      if (response.resultCode === "S") {
        if (response.listBlockOrderCredit && response.listBlockOrderCredit.length > 0) {
          
          this.awnCount = 0;
          this.wdsCount = 0;

          response.listBlockOrderCredit.forEach((res, idx) => {
            this.findLength(res);
            if (res.listAutoUnblockOrderDetailBean[0].company === 'AWN') {
              this.awnData.listBlockOrderCredit.push(res)
              this.awnData.overDue = response.overDue;
            } else if (res.listAutoUnblockOrderDetailBean[0].company === 'WDS') {
              this.wdsData.listBlockOrderCredit.push(res);
              this.wdsData.overDue = response.overDue;
            }

            console.log(idx);
            console.log(response.listBlockOrderCredit.length)
            console.log(this.awnData);
            console.log(this.wdsData);
            if (idx === response.listBlockOrderCredit.length - 1) {
              this.company.next('AWN')
              this.pageLoadingService.closeLoading();
            }
          })
        } else {
          this.pageLoadingService.closeLoading();
          this.modalService.alert([true, 'Data not found', false, '/index']);
        }
      } else {
        this.pageLoadingService.closeLoading();
        this.modalService.alert([true, 'QueryAccBlock Fail', false, '/index']);
      }
    })
  }

  findLength(data) {
    data.listAutoUnblockOrderDetailBean.forEach(value => {
      if (value.company === 'AWN') {
        this.awnCount++;
      } else {
        this.wdsCount++;
      }
    })
  }

  getWDSdata() {
    return this.wdsData;
  }

  getAWNdata() {
    return this.awnData;
  }
}



