import { Component, OnInit } from '@angular/core';
import { ArsOder } from './models/ars-order.model';
import { ArsOrderService } from './service/ars-order.service';
import { UserInfo } from 'app/services/userInfo';
import { PageLoadingService } from 'client-mypartner-lib/src/components/page-loading/page-loading.service';
import { Router } from '@angular/router';
import * as FileSaver from 'file-saver';
@Component({
  selector: 'app-ars-order',
  templateUrl: './ars-order.component.html',
  styleUrls: ['./ars-order.component.scss']
})
export class ArsOrderComponent implements OnInit {

  arsList: ArsOder;
  onSearch: boolean;
  sortMode = 'DES'
  selectedArs: any;
  showAlert = false;
  alertText: string;
  locationCode: string;
  mode = 'get';

  constructor(private arsOrderService: ArsOrderService, private user: UserInfo, private pageLoadingService: PageLoadingService, private router: Router) { }

  ngOnInit() {
    console.log(this.selectedArs);
    this.pageLoadingService.openLoading();
    this.locationCode = this.user.getLocationCode() ? this.user.getLocationCode().toString() : '';
    this.getArsOrders();
  }

  getArsOrders() {
    this.mode = 'get';
    this.arsOrderService.getArsOrders(this.locationCode).subscribe(val => {
      console.log(val)
      if (val.resultData && val.resultData.length > 0) {
        this.arsList = this.arsOrderService.sortArsList(val.resultData, this.sortMode);
        this.pageLoadingService.closeLoading();
      }
    }, err => {
      console.log(err);
      this.alertText = "ไม่สามารถแสดง Order ได้กรุณาลองใหม่อีกครั้ง";
      this.showAlert = true;
      this.pageLoadingService.closeLoading();
    });
  }

  onClickSearch() {
    console.log('on click search');
    this.onSearch = !this.onSearch;
  }

  onClickSort() {
    console.log('on click sort =>', this.sortMode);
    this.sortMode = this.sortMode === 'DES' ? 'ASC' : 'DES';
    this.arsList = this.arsOrderService.sortArsList(this.arsList, this.sortMode);
  }

  onSearchData(data) {
    this.mode = 'search';
    this.pageLoadingService.openLoading();
    this.arsOrderService.searchArsOrder(data).subscribe(val => {
      this.arsList = this.arsOrderService.sortArsList(val.resultData, 'DES');;
      this.onSearch = false;
      this.pageLoadingService.closeLoading();
    }, err => {
      console.log(err);
      this.alertText = "ไม่พบข้อมูล Order ที่ค้นหา";
      this.showAlert = true;
      this.pageLoadingService.closeLoading();
    });
  }

  selectedArsList(data) {
    console.log(data)
    this.selectedArs = data;
  }

  closeAlert(event) {
    // if (this.mode === 'get') {
    //   this.router.navigate(['/index'])
    // }
    this.showAlert = false;
  }

  exportExcel(data) {
    this.pageLoadingService.openLoading();
    this.arsOrderService.exportExcel(data).subscribe(val => {
      if (val.resultCode === "20000") {
        const blobType: string = 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8';
        const blob = new Blob([new Uint8Array(val.data.blob.data)], { type: blobType });
        // FileSaver.saveAs(blob, `Report_of_${this.startDate}_to_${this.endDate}.xlsx`);
        // FileSaver.saveAs(blob, `Report.xlsx`);
        FileSaver.saveAs(blob, `${data.orderNo}.xlsx`);
        this.pageLoadingService.closeLoading();
      } else {
        this.alertText = "ไม่สามารถสร้าง Excel ได้ในขณะนี้";
        this.showAlert = true;
        this.pageLoadingService.closeLoading();
      }
    }, err => {
      console.log(err)
      this.alertText = "ไม่สามารถสร้างเอกสารได้ในขณะนี้";
      this.showAlert = true;
      this.pageLoadingService.closeLoading();
    })
  }

}



